html,body{
	font: 18px/1.4 sans-serif;
	font-family: "lato";
	font-weight: 300;
	-webkit-font-smoothing:antialiased;
	overflow-scrolling: touch;
}

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
	display: none !important;
}

h1,h2{
	&:after{
		content:'';
		display:block;
		width:110px;
		border-top: 2px solid $color-blue;
		margin: 15px auto;
	}
	@media #{$medium-up} {
		&:after{
			width:80px;
			border-top: 5px solid $color-blue;
			margin: 30px auto;
		}
	}

}

.rich {
	header {
		h1 {
			&:after {
				border: none;
			}
		}

		h1 + p {
			margin-bottom: 0;
		}

		&:after {
			content: '';
			display: block;
			width: 110px;
			border-top: 2px solid $color-blue;
			margin: 15px auto;
		}
		@media #{$medium-up} {
			&:after {
				width: 80px;
				border-top: 5px solid $color-blue;
				margin: 30px auto;
			}
		}

		+p{
			margin-bottom: 50px;
		}
	}
}



h2{

	&:after{
		width:60px;
		border-top: 2px solid $color-blue;
		margin: 15px auto;
	}

	@media #{$medium-up} {
		&:after{
			width:100px;
			border-top: 4px solid $color-blue;
			margin: 20px auto;
		}
	}
}

.__white{
	h1,h2{
		color:black;
	}
}

.__blue{
	h1,h2 {
		&:after {
			border-color: $color-orange;
		}
	}

	h3,h4,h5{
		color:white;
	}
}
.header{
	&.__white{
		background-color:white;
	}
}
.rich{
	h1 {
		& + p{
			> div, a{
				margin-top:50px;
				@media #{$medium-up} {
					margin-top:100px;
				}
			}
		}

		& + p + p{
			>  div, a{
					margin-top:50px;
					@media #{$medium-up} {
						margin-top:80px;
					}
			}
		}

		& + p{
			font-size: 24px;
			margin-bottom: 50px;
		}

	}
}

.section_intro_text{
  font-size: 18px;
  @media #{$medium-up} {
    font-size: 20px;
  }
  @media #{$large-up} {
    font-size: 24px;
  }
}



//button types

.link{

	&.__btn{
		span{
			padding-top: 10px;
			vertical-align: middle;
		}
		i{
			&.fa-long-arrow-right{
				padding-left: 20px;
				font-size: 24px;
				vertical-align: middle;
			}
		}
	}
}



//Round image icons

.img-round {
	position: relative;
	cursor: auto;
	margin-bottom: 20px;
	img {
		width: 100%;
		height: auto;
	}
	.img-block {
		position: absolute;
		overflow: hidden;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 50%;
		transition: 1s;
	}
}

#app{
	position: relative;
	background: transparent;

}

#content{
	position: relative;
	overflow-x: hidden;
	width:100%;
	height:auto;
}

#content[data-state="app.content.main"]{
	//perspective: none !important;
}

//GASP animation setup
.gsapify-router-in-setup,.gsapify-router-in{
	position: absolute;
	overflow: hidden;
	z-index: 1;
	top:0;
}

.gsapify-router-out-setup,.gsapify-router-out {
	position: absolute;
	overflow: hidden;
	z-index: 5;
	top:0;
}


.gsapify-router-in, .gsapify-router-out{
	position: absolute;
	overflow: hidden;
	top:0;
}












