@import "slick";
@import "slick-theme";


.slider{
	visibility: hidden;
}

.slick-initialized { visibility: visible; }

.slider-holder{
	overflow: hidden;
	margin: 0 auto;
	padding: 10% 5%;

	.slide{
		> .caption{
			width: 100%;
			opacity: 0;
			text-align: center;
			padding: 10px 0;

		}
	}

}

.rich ul{
	&.slick-dots {
		padding:0;
	}
}

.slick-next, .slick-prev {
  font-family: FontAwesome;
  background: $color-medium-grey;
  width:50px;
  height: 50px;
  @media #{$small-up}{
    width:30px;
    height: 30px;
  }
  @media #{$medium-up}{
    display:none!important;
    width:50px;
    height: 50px;
  }
  &:before {
    font-family: FontAwesome;
    font-size: 30px;
    color:#969696;
    @media #{$small-up}{
      font-size: 15px;
    }
    @media #{$medium-up}{
      font-size: 30px;
    }
  }
  &:hover,&:focus{
    background: $color-medium-grey;
    opacity:0.9;
  }
}
.slick-next{
  &:before {
    content:"\f105";
    padding-left: 5px;
  }
}
.slick-prev{
  &:before {
    content:"\f104";
    padding-right: 5px;
  }
}

@media #{$small-only}{
  .slider-holder{
    padding:10% 0;
    .columns{
      padding:0;
    }
  }
  .slick-next{
    right:0;
    margin-top:-40px;
  }
  .slick-prev{
    left:0;
    margin-top:-40px;
  }
}

.slick-dots {
	position: absolute;
	bottom: 15%;

	list-style: none;
	display: block;
	text-align: center;
	padding: 0;
	width: 100%;
  @media #{$small-only}{
    display:none!important;
  }
	li {
		position: relative;
		display: inline-block;
		height: 20px;
		width: 20px;
		margin: 0 5px;
		padding: 0;
		cursor: pointer;
		button {
			border: 0;
			background: transparent;
			display: block;
			height: 20px;
			width: 20px;
			outline: none;
			line-height: 0px;
			font-size: 0px;
			color: transparent;
			padding: 5px;
			cursor: pointer;
			&:hover, &:focus {
				outline: none;
				&:before {
					opacity: $slick-opacity-on-hover;
				}
			}
			&:before {
				position: absolute;
				top: 0;
				left: 0;
				content: '';
				width: 20px;
				height: 20px;
				line-height: 20px;
				text-align: center;
				color: $slick-dot-color;
				border-radius: 10px;
				background: $slick-dot-color;
				-webkit-font-smoothing: antialiased;
				opacity: $slick-opacity-default;
				-moz-osx-font-smoothing: grayscale;
			}
		}
		&.slick-active button:before {
			background: $slick-dot-color-active;
			opacity: $slick-opacity-default;
			border: 2px solid white;
			transition-property: border;
			transition: 0.2s;
		}
	}
}

.slick-active{
	.caption{
		@include fadeIn(0.8s,0.5s);
	}
}

.slider .slide {
  width: 100%;
  height: 100%;
  max-width: 100%;
}

.slide img {
  margin:0 auto;
}
