// Menu
// ====

.menu {
  @extend %clearfix;
  list-style: none;
  font-family: $headings;
  font-size: 16px;
  font-weight: $light;
  margin: 0;
  padding: 6px 0;

  &_i {
    float: left;
    + .menu_i {
      @media #{$medium-up} {
        margin-left: 2.5%;
      }

    }

    &.__current {

      > .menu_i_inner {
        color:$color-blue;
        margin: -6px -8px;
        padding: 6px 8px;
      }
    }
  }

  &_i_inner {
    display: block;
    &.__complex {
      @extend .link.__expanded;
    }
  }

}

.sub_menu{
  list-style: none;
  display:block;
  margin-bottom: 30px;
  clear:both;

  .menu_sub_i_inner{

    a{
      text-decoration: none;
    }

      .sub_u{
        float:left;
        margin-right: 10px ;
        font-size: 14px;
      }
  }
}


.rich{

  ul.sub-menu{
    display: block;
    list-style: none !important;
    font-family: $headings;
    font-size: 16px;
    font-weight: $light;
    padding: 28px 0;
    margin: 0 auto;
    @media #{$small-only} {
      display: none;
    }



    .link_u{
      color:white;
      :hover{
        color:$color-blue;
      }
    }

    @media #{$medium-up} {
      font-size: 1.4vw;
    }

    @media #{$xlarge-up} {
      font-size: 18px;
    }

    &.expanded {
      @media #{$small-only} {
        display: block;
        margin-top: 80px;
        background-color: rgba(black, 0.9);
        border-bottom: 1px solid  rgba(white, 0.1);
        @include stretchDown(0.3s);
        @for $i from 1 through 10 {
          .menu_i:nth-child(#{$i}){
            @include  fadeInDown(0.5s,#{$i * 0.1}s)
          }
        }
      }
    }
    &.collapsed {
      @media #{$small-only} {
        margin-top: 80px;
        background-color: rgba(black, 0.9);
        @include compressUp(0.3s);
      }

      @media #{$medium-up} {
        display:block;
      }
    }

    .menu_i {
      display:block;
      margin-bottom: 20px;
      float:none;
      opacity: 0;

      &:after{
        content:' ';
        display:block;
        width:70%;
        margin: 20px auto;
        border-bottom: 1px solid  rgba(white, 0.5);
      }

      &:last-child{
        &:after{
          display: none;
        }
      }

      @media #{$medium-up} {
        display:inline-block;
        float:none;
        opacity: 1;
        &:after{
          display:none;
        }
      }
    }





  }
}


.menu-bar {


  .btToggle{
    position:absolute;
    top:-4px;
    left:30px;
    z-index: 200;
  }

  .expanded {
    display: block;
    .menu-social-holder{
      display: none;
    }
  }


  .collapsed {
    display: none;





    &.open{
      display: table;
      position:fixed;
      width:100%;
      height: 100vh;
      top:0;
      bottom:0;
      left:0;
      right:0;
      background: white;
      z-index: 100;

      @include fadeIn();

      .menu_i {
        float:none;
        width:100%;
        text-align: center;
        margin: 0;
        opacity: 0;
      }

      @for $i from 1 through 10 {
        .menu_i:nth-child(#{$i}){
          @include  fadeInDown(0.5s,#{0.5+($i * 0.1)}s)
        }
      }

      &.closing{
        @include fadeOut(1.5s);

        .menu_i{
          @include  fadeOut(0.5s)
        }

      }



      .menu{
        display:table-cell;
        vertical-align: middle;

        .__complex.menu_i_inner{
          padding:12px;

          @media #{$medium-up} {
            padding-top: 12px;
            padding-bottom: 12px;
          }
          @media #{$large-up} {
            padding-top: 25px;
            padding-bottom: 25px;
          }
        }

        .menu_i.__current>.menu_i_inner{
          color: $color-blue;
          font-size: 28px;
          padding:12px;
            @media #{$large-up} {
              font-size: 40px;
              padding-top:25px;
              padding-bottom:25px;
          }
          }

        .link{
          &.__grey{

            &:hover{
              .link_u {
                color: $color-blue;
              }
            }
            .link_u{
              font-size: 28px;
              @media #{$large-up} {
                font-size: 40px;

              }
            }

          }
        }
      }
    }
    .menu-social-holder{
      position: fixed;
      bottom:14%;
      left:0;
      right:0;
      text-align: center;
      @media #{$small-only} {
        bottom: 8%;

      }

      a{
        display: inline-block;
        text-align: center;
        width:30px;
        font-size: 22px;
        margin:0 18px;
        cursor:pointer;

        .fa-facebook{
          color:#3b5998;
        }
        .fa-twitter{
          color:#0099e5;
        }
        .fa-youtube-play{
          color:#cd201f;
        }
        .fa-linkedin{
          color:#0077b5;
        }
      }

    }
  }

 &.docked{
   &.collapsed {
     &.open{
       top:80px;
     }

    }
  }
}

.menu-bar  .docked.collapsed.open{
  top:80px;
}

.sub-menu-holder{
  position:absolute;
  width:100%;
  bottom: 2vh;

  @media #{$medium-up} {
    bottom: 21vh;
  }
}

.sub-menu-bar{
  width: 100%;
  height: 80px;
  text-align: center;
  position: absolute;
  opacity: 1;
  z-index: 3;
  
  .btToggle{
    position: absolute;
    font-size: 43px;
    color:white;
    top: 10px;
    right: 30px;
    z-index: 200;
    span{
      position: absolute;
      top:32%;
      right:35px;
      font-size: 18px;
      font-weight: bold;
    }
  }



  &.pin-pined{
    position:fixed;
    top:0;
    opacity: 1;
    background: rgba(0,0,0,0.9);
    @media #{$medium-up} {
      padding: 0 10px 0 50px;
    }
    transform: translateZ(0);
  }
}




