.services-main {

	.service-nav {
		position: relative;

		@media #{$small-only} {

			.section.__vertical .wrap {
				margin-top: 50px;
				transform: none;
			}

			.body-fixed {
				display: none;
			}

		}

		@media #{$medium-up} {
			height: 100vh;
			z-index: 1;
			background: white;

			> .rich {
				position: fixed;
				width: 100%;
				background: white;
			}
		}
	}

	.service-sub {
		position: relative;
		z-index: 2;
	}
}

#main {

	.service-header {
		height: auto;
		@media #{$medium-up}{
			height: 25vh;
		}
		h2 {
			font-size: 28px;
			padding-top: 80px;
			@media #{$medium-up}{
				font-size: 48px;
			}
		}
	}

	.service {
		height: auto;
		@media #{$medium-up}{
			height: 75vh;
		}

		@media #{$small-only} {
			&.section.__vertical .wrap {
				margin-top: 50px;
				transform: none;
			}
		}
	}
}

.services-menu {

	h2 {
		font-size: 28px;
		margin-bottom: 50px;

		@media #{$medium-up} {
			font-size: 3vw;
		}

		@media #{$xlarge-up} {
			font-size: 48px;
		}
	}
	ul {
		list-style: none;
		padding: 0;
		margin: 0;

		li {
			margin: 0 0;

			@media #{$medium-up} {
				margin: 0 5%;
			}

			@media #{$xlarge-up} {
				margin: 0 15%;
			}

			button {
				width: 100%;
				margin: 0 0 0.5rem;
			}
		}
	}
}

#conditions {
	h2 {
		font-size: 30px;
		@media #{$medium-up} {
			font-size: 72px;
		}
	}
}

#gettinghere {
	h2 {
		font-size: 24px;
		padding: 40px 0 20px 0;
		&:after {
			border: none;
		}
		@media #{$medium-up} {
			font-size: 48px;
		}
	}
}
