/* ==========================================================================
   IE css
   ========================================================================== */

.oldBrowser{
  position: fixed;
  background: #fff;
  color: #232323;
  top:0;
  left: 0;
  width:100%;
  height: 100%;
  z-index: 100;

  .content{
    margin: 0 auto;
    max-width: 960px;
    text-align: center;

    > h1{
      margin: 30px 0 30px 0;
      background: url("/assets/images/project/logo.png") no-repeat center center;
      height: 100px;
      color:transparent;

    }
    h1:after,h2:after {
      border-top:none;
    }
    p{
      font-size: 12px;
      margin-bottom: 50px;
    }
  }

  .footer{
    font-size: 12px;
    padding-top: 30px;
    clear:left;

    a {
      color:#232323;
    }
  }

  ul#browserlist{

    padding: 0;
    width:960px;


    li{
      width: 170px;
      font-size: 12px;
      padding: 10px 0;
      margin-left:10px;
      display: inline-block;
      float: left;

      &:hover{
        background: darken(#fff,10%);
      }

      a {  h1{
        font-size: 12px;
        color: #232323;
        text-transform: uppercase;
        text-decoration: none!important;
        background: darken(#fff,20%);
        padding: 0 5px;
        height:38px;
        line-height: 38px;
      }
        color:#232323;
        text-decoration:none;

      }

      .icon{
        background: url("/assets/images/browsericons.png") no-repeat;
        width:125px;
        height: 135px;
        margin: 0 auto 20px auto;
      }

      &#chrome .icon{
        background-position:  0 0;
      }
      &#firefox .icon{
        background-position-x:  -125px;
      }

      &#opera .icon{
        background-position-x:  - (125px * 2);
      }

      &#safari .icon{
        background-position-x:  - (125px * 3);
      }

      &#ie .icon{
        background-position-x:  - (125px * 4);
      }

    }
  }

}

.browser-ie {

  /* Menu button */
  .lines {
    height: 0.24em;
  }

  /* block-tabs - icons */

  .tabs{
    ul{
      li{
        .icon{
          .icon-button{
            .icon-img{
              padding:25%;
            }
          }
        }
      }
    }
  }

}
