// Colors
$color-blue: #007bc4;
$color-red: #C00;
$color-yellow: #FC0;
$color-grey:#a0a0a0;
$color-light-grey:#ebebeb;
$color-medium-grey:#4b4b4b;
$color-dark-grey:#333333;
$color-dark-blue:#00629c;
$color-orange:#fa8a38;

// Font stacks
$consolas: "lato";
$headings: "open-sans";


// Font weights:
$thin:        100;
$extra-light: 200;
$ultra-light: 200;
$light:       300;
$normal:      400;
$regular:     400;
$medium:      500;
$semi-bold:   600;
$demi-bold:   600;
$bold:        700;
$extra-bold:  800;
$ultra-bold:  800;
$black:       900;
$heavy:       900;

//GRID
// We use this to control whether or not CSS classes come through in the gem files.
$include-html-classes: true;
// $include-print-styles: true;
$include-html-global-classes: $include-html-classes;


$row-width: 1700px;
$total-columns: 12;
$column-gutter: 30px;

$row-top-bottom:30px;

//SLICK
// Setting for slick JS.

$slick-font-path: '/assets/fonts/';
$slick-loader-path: '/assets/images/';
$slick-dot-color-active: $color-blue;
$slick-dot-color: white;
$slick-dot-size:18px;
$slick-opacity-default:1;
$slick-opacity-on-hover:1;

