.body-fixed{
	position:fixed;
	width:100vw;
	height:100vh;
	transform: translateZ(0);
	z-index: 0;

	&.end{
		position:absolute;
		bottom:0;
	}
}

#main{

	.indicator-scroll{
		bottom:-100px;
	}

}

.feature{
	h1{
		color:#000;
		span{
			display:block;
			font-weight: $light;
		}
		&:after{
			display:none;
		}
		font-weight: $normal;
	}
  .main_subtitle{
    margin:20px 0;
    font-size: 18px;
    @media #{$medium-up} {
      font-size: 24px;
      margin:50px 0;
    }
  }
}

.rich{
  .parallax{
    p.l{
      font-size: 18px;
      @media #{$medium-up} {
        font-size: 20px;
      }
      @media #{$large-up} {
        font-size: 24px;
      }
    }
  }
}




.side-social-holder{
  display: none;
  @media #{$large-up} {
    display: block;
    position: fixed;
    left:25px;
    top:50%;

    a{
      display: block;
      text-align: center;
      width:30px;
      font-size: 25px;
      padding: 0.5em 0;

      .fa-facebook{
        color:#3b5998;
      }
      .fa-twitter{
        color:#0099e5;
      }
      .fa-youtube-play{
        color:#cd201f;
      }
      .fa-linkedin{
        color:#0077b5;
      }
    }
  }

}





