// Wrap
// ====

.wrap {
  margin-left: auto;
  margin-right: auto;
  max-width: $row-width;
  padding-left: $layout-gutter;
  padding-right: $layout-gutter;

  &.__center{
    text-align: center;
  }

  &.__full {
    position:relative;
    width:100%;
    padding: 0;
    margin: 0;
    max-width: none;
    min-width: 0;
    overflow: hidden;

    &.__center {
      text-align: center;
    }

    &.__vertical {
      div {
        display: table;
        width: 100%;
        height: 100%;
        vertical-align: middle;

        > .wrap {
          display: table-cell;
          vertical-align: middle;
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  &.__large-fixed{
    position:fixed;
    width:100%;
    height: 100vh;
    bottom:0;
    overflow: hidden;
  }

  &.__black{
    background-color: #000;
    color:#fff;
  }

  &.__white{
    background-color: #fff;
    color: $color-dark-grey;
  }

  &.__white-text{
    background-color: transparent;
    color: white;
  }

  &.__blue{
    color: #FFF;
    background-color: $color-blue;
  }

  &.__trans{
    color: #000;
    background-color: rgba(255,255,255,0.5);
  }

  &.row-tbm{
    .row {
      padding: $row-top-bottom 0;
    }
  }

  .__blue{
    color: #FFF;
    background-color: $color-blue;
  }

}
