.block-testimonials{
  background: white;
}


#testimonials{

  h1{
    margin-top: 10vh;
  }

  .section_intro_text{
    font-size: 18px;
    @media #{$medium-up} {
      font-size: 20px;
    }
    @media #{$large-up} {
      font-size: 24px;
    }
  }

	.corner{
		position: relative;
		width:25px;
		height: 25px;
		margin:0 auto;

		&:after{
			content:" ";
			display:block;
			position:absolute;
			background-color: white;
			bottom:-12.5px;
			width:25px;
			height: 25px;
			transform:rotate(45deg);
			margin: 0 auto;
			z-index: 2;
		}
	}

	.slider{
		background: $color-dark-grey;
		&.columns{
			padding: 0;
		}
		.slick-slider{
			margin: 0;
		}
	}

	.slick-prev, .slick-next {
    top:22%;
    margin-top:-10px;
    @media #{$medium-up}{
      display:inline-block!important;
    }
    @media #{$large-up}{
      top:50%;
      margin-top:-50px;
    }
	}
  .slick-next{
    right:0;
  }
  .slick-prev{
    left:0;
  }


	.slick-dots {
    bottom:0;
    @media #{$large-up} {
      bottom: 5%;
    }
	}

	.slide-track{

	}

	.slick-slide{
		padding:0;
		color:white;
	}

	.slide{

		h3{
			color:white;
			font-size: 18px;
      @media #{$medium-up} {
        font-size: 25px;
      }
      @media #{$large-up} {
        font-size: 45px;
      }
		}

		li{
			font-size: 16px;
      text-align: center;
      margin-bottom: 5px;
      @media #{$medium-up} {
        font-size: 25px;
      }
      @media #{$large-up} {
        margin-bottom:25px;
      }
		}

		overflow: hidden;
		&.columns{
			padding:0;
		}

		.info{
      float: none;
      width:90%;
			position:relative;
			height: auto;
      margin:0 auto;
      padding-bottom: 5vh;
      text-align: center;
      @media #{$medium-up} {
        width:90%;
      }
      @media #{$large-up} {
        width:50%;
        height:80vh;
      }
      ul{
        padding:6%;
        @media #{$medium-up} {
          padding:6%;
        }
        @media #{$large-up} {
          padding:6vh 10vh 3vh 10vh;
        }
      }
		}

		.img{
			position: relative;
			height: auto;
      float: none;
      width:100%;
      @media #{$large-up} {
        height: 80vh;
        width:50%;
      }

			&.columns{
				padding:0;
			}
			
			.bg-full{
				top:0;
				position: absolute;
				width: 100%;
				height: 100%;
				background-size: cover;
				background-repeat: no-repeat;
				background-position: top center;
        @media #{$large-up} {
          background-position: center center;
        }
			}
			img{
        height:280px;
        visibility: hidden;
        @media #{$medium-up} {
          height: 480px;
        }
        @media #{$large-up} {
          display:none;
        }
      }
		}
		.block-quote{
			//position: absolute;
			//overflow: visible;
			//width:100%;
			//top:44%;
      //left:0;
			//height:1px;
			z-index: 2;
      opacity: 1;
      @media #{$medium-up} {
        //top:44vh;
      }

			blockquote{
        position: relative;
				width: 100%;
				border: none;
				color:white;
				margin: 0 auto;
				font-size: 14px;
				font-size: 3vm;
				font-size: 4vmin;
				font-style: italic;
				max-width: 840px;
				padding:5% 6%;
				background: $color-medium-grey;
        z-index: 2;
        float: none;
        left:auto;
        right:auto;
        @media #{$medium-up}{
          font-size: 30px;
          font-size: 3vm;
          font-size: 3vmin;
        }
        @media #{$large-up}{
          margin: 0 -60px;
        }

			}
		}

		button{
			position:relative;
			bottom:10%;
			margin: 6% 0;
      padding: 10px 25px;
      @media #{$medium-up}{
        padding: 20px 40px;
      }
      @media #{$large-up}{
        clear:both;
      }
      i{
        top:50%;
        margin-top:-15px;
        @media #{$medium-up}{
          margin-top:-25px;
        }
      }
      span{
        margin-left: 25px;
        @media #{$medium-up}{
          margin-left: 34px;
        }
      }
		}

		&.odd{
      .img{
        @media #{$large-up} {
          float: left;
        }
      }
			.info{
        @media #{$large-up} {
          float: right;
        }
				li{
          @media #{$large-up} {
            text-align: left;
          }

				}
			}
      button{
        @media #{$large-up} {
          position: relative;
          float: left;
          margin-left: 10vh;
        }
      }
			.block-quote {
				blockquote {
					//margin-left: -10%;
          @media #{$large-up} {
            float: left;
            position: relative;
            left: -60px;
            right: auto;
          }
				}
			}
		}

		&.even{
      .img{
        @media #{$large-up} {
          float: right;
        }
      }
			.info{
        @media #{$large-up} {
          float: left;
        }
				li{
          @media #{$large-up} {
            text-align: right;
          }
				}
			}
			button{
        @media #{$large-up} {
          position: relative;
          float:right;
          margin-right: 10vh;
        }
			}
			.block-quote {
				blockquote {
					//margin-left: 25%;
          @media #{$large-up} {
            float: right;
            right: -60px;
            left: auto;
          }

				}
			}
		}
	}

}
