.block-tab{

	.tab-list{
		@media #{$small-only} {
			padding:0;
		}
	}

	.pointer {
		position: absolute;
		width: 20px;
		height: 20px;
		top: -10px;
		z-index: 2;
		opacity: 1;
		&:before {
			content: ' ';
			display: block;
			width: 20px;
			height: 20px;
			transform: rotate(45deg);
			background: white;
			margin: 0 auto;
		}
	}

}


.tabs{

  h2{
    font-size: 30px;
    @media #{$medium-up} {
      font-size: 60px;
    }
    @media #{$large-up} {
      font-size: 72px;
    }
  }

	padding-top:5%;

	.content{
		width: 100%;
		padding:40px 0;
		min-height: 20vh;
		> div > div{
		}

	}

	.sub-content-holder-small{
		overflow: hidden;
		height: 0;
	}

	.sub-content-holder{
		position: relative;
		padding:40px 0;
		min-height: 20vh;

		.content{
			position:absolute;
			top:0;
			opacity: 0;
		}
	}



	ul {
		padding-left: 0;
		margin-top: 40px;
		margin-bottom: 0;
    @media #{$small-only} {
      &:after {
        padding-bottom: 10px;
      }
    }
		li {
			position: relative;
			cursor: pointer;

			@media #{$small-only} {
				border-top: rgba(255, 255, 255, 0.5) solid 1px;
				margin-bottom: 0;
				padding: 0;

				&:last-child{
					border-bottom: rgba(255, 255, 255, 0.5) solid 1px;
				}
			}

			.row{
				margin: 0;
				> div{
					padding: 0 15px;
				}
			}

      h4{
        font-size: 16px;
        @media #{$medium-up} {
          font-size: 1.2vw;
          line-height: 12px;
        }
        @media #{$large-up} {
          font-size: 18px;
          line-height: 18px;
        }
      }

      .icon-no-title{
        color:white;
        @media #{$medium-up} {
          color:transparent;
          height:1px;
          margin:0;
        }
      }

			@media #{$medium-up} {
			.icon{
				position: relative;
				cursor: pointer;
				margin-bottom: 20px;
					img{
						width: 100%;
						height: auto;
					}
        .icon-button {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          background: $color-dark-blue;
          border: 5px solid $color-dark-blue;
          transition: 1s;

          .icon-img {
            padding: 22%;
            font-size:7vw;
            .bf-outpatients-followup_referrals{
              margin-left: -7px;
            }
          }
          .icon-no-img {
            display:table;
            height:100%;
            width:100%;
            h4{
              vertical-align: middle;
              display: table-cell;
              padding: 5%;
            }
          }
        }

        .image-button {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 50%;
          transition: 1s;

          .icon-img {
            padding: 5%;
          }
        }

        &:hover {
          .icon-button {
            border: 5px solid white;
            transition-property: border;
            transition: 1s;
          }
        }

        &:hover {
          .image-button {

          }
        }
		  }

	    }
      @media only screen and (min-width: 2000px) {
        .icon{
          .icon-button {
            .icon-img {
              font-size:6vw;
            }
          }
        }
      }

      @media only screen and (min-width: 2400px) {
        .icon{
          .icon-button {
            .icon-img {
              font-size:5vw;
            }
          }
        }
      }

			&.active{

			}
		}
	}
  .small_range_service {
    width:50%;
    margin:0 auto;
    @media #{$small-up} {
      width:100%;
    }
    @media #{$medium-up} {
      width:65%;
    }
    @media #{$large-up} {
      width:50%;
    }
  }
  .large_range_service {
    li{
      @media #{$medium-up} {
        padding: 0 0.125rem 1.25rem;
      }
    }
    h4{
      @media #{$medium-up} {
        font-size: 1vw;
        line-height: 10px;
      }
      @media #{$large-up} {
        font-size: 16px;
        line-height: 18px;
      }
    }
  }


  .wrap{
    max-width:100%;
  }
}
