// Button
// ======

.btn {
  @extend .btn.__m;
  @extend .btn.__white;
  border-radius: 60px;
  border-style: solid ;
  border-width: 2px;
  font-type:$headings;
  font-weight:$extra-bold;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  user-select: none;
  text-transform: uppercase;

  &:focus {
    box-shadow: 0 0 6px 2px rgba(black, 0.1);
    outline: 0;
  }

  &:disabled {
    cursor: default !important;
    opacity: 0.6;
    pointer-events: none;
  }

  // Modifiers
  // ---------

  // ### Sizes

  &.__m {
    @include font-height(13px, 16px);
    padding: 5px 12px;
  }

  &.__l {
    @include font-height(12px, normal);
    padding: 15px 40px;
  }

  &.__xl {
    @include font-height(14px, 18px);
    padding: 10px 25px;

    @media #{$medium-up} {
      @include font-height(18px, 22px);
      padding: 20px 50px;
    }

  }

  // ### Colours

  &.__white {
    background: white;
    border-color: black;

    color: #333;

    &:hover:not(:disabled) {
      color:white;
      background: $color-blue;
      border-color: $color-blue;
    }

    &.__active,
    &:active:not(:disabled) {
      background: white;
    }

    &.__active,
    &.__active:hover {
      background: $color-blue;
      border-color: $color-blue;
    }
  }

  &.__white_trans {
    background: transparentize(#fff,1);
    border-color:#fff;
    color: #fff;

    &:hover:not(:disabled) {
      background: transparentize($color-blue,0);
      border-color:$color-blue;
    }

    &.__active,
    &:active:not(:disabled) {
      background: transparentize($color-blue,0);
      border-color:$color-blue;
    }

    &.__active,
    &.__active:hover {
      background: transparentize($color-blue,0);
      border-color:$color-blue;
    }
  }

  // ### Misc.

  &.__block {
    display: block;
    width: 100%;
  }
}

.block-feature-animated {
  .btn{
    &.__xl {
      @include font-height(14px, 18px);
      padding: 10px 25px;

      @media #{$large-up} {
        @include font-height(18px, 22px);
        padding: 20px 50px;
      }
    }
  }
}

$color : black;
$transition: .3s;// increase this to see the transformations happen in slow motion

// ==================================== grid button ====================================//

$base : 1rem;
$space : $base/4;

//common to both grid buttons
.grid-button {
  padding: $base*2;
  cursor: pointer;
  user-select: none;
}
//common to both grid buttons
.grid-button{
  .grid{
    width: $base;
    height: $base;
    background: $color;
    color: $color; /* Not in use when the colors are specified below */
    transition: $transition;
  }
}
.grid-button.close{
  .grid{
    -webkit-transform: rotate3d(0,0,1,-45deg)  scale3d(.8,.8,.8);
    transform: rotate3d(0,0,1,-45deg)  scale3d(.8,.8,.8);
  }
}
//grid rearrange only
.grid-button.rearrange {
  .grid{
    box-shadow:
    -($base+$space) 0 -($base+$space),
    0 0 -($base+$space),
            ($base+$space) (-($base + $space)),
    -($base+$space) 0,
    $base+$space 0,
    -($base+$space) ($base + $space),
    0 ($base+$space),
            ($base+$space) ($base + $space);
  }
}
.grid-button.rearrange.close {
  .grid{
    box-shadow:
    0 0 -$base,
    0 0 -$base*2,
    $base 0,
    -$base*2 0,
    $base*2 0,
    -$base 0,
    0 $base*2,
    0 $base;
  }
}
.grid-button.collapse{
  .grid{
    box-shadow:
    -($base+$space) 0,
    -($base+$space) ($base+$space),
    $base+$space 0,
            ($base+$space) (-($base+$space)),
    0 0 -($base+$space),
    -($base+$space) 0 -($base+$space),
    0 ($base+$space),
            ($base+$space) ($base+$space);
  }

}
.grid-button.collapse.close {
  .grid{
    box-shadow:
    -$base 0,
    0 0 transparent,
    $base 0,
    0 0 transparent,
    0 0 -$base,
    0 0 transparent,
    0 $base,
    0 0 transparent;
  }
}


/* ====================== lines button ==================================================*/

$button-size : 1.5rem;
//common to all three-liners
.lines-button {
  //padding: $button-size/2 $button-size/4;
  transition: .3s;
  cursor: pointer;
  user-select: none;
  border-radius: $button-size/7;
  /* */
  &:hover {
    opacity: 1;
  }
  &:active {
    transition: 0;
  }
}
//common to all three-liners
@mixin line {
  display: inline-block;
  width: $button-size;
  height: $button-size/7;
  background: $color;
  //border-radius: $button-size/14;
  transition: $transition;
}
//common to all three-liners
.lines {
  @include line;
  position: relative;

  &:before, &:after {
    @include line;
    position: absolute;
    left:0;
    content: '';
    -webkit-transform-origin: $button-size/14 center;
    transform-origin: $button-size/14 center;
  }
  &:before {
    top: $button-size/3.9;
  }
  &:after {
    top: -$button-size/3.9;
  }
}

.lines-button{
  &._invert{
    .lines {
      background: white;
      &:before {
        background: white;
      }
      &:after {
        background: white;
      }
    }
  }
}

//common to all three-liners
.lines-button:hover {
  .lines {
    background: $color-blue;
    &:before {
      //top: $button-size/3.5;
      background: $color-blue;
    }
    &:after {
      //top: -$button-size/3.5;
      background: $color-blue;
    }
  }
}
.lines-button.close {
  -webkit-transform: scale3d(.8,.8,.8);
  transform: scale3d(.8,.8,.8);
}
// for both the arrow up and left icons
.lines-button.arrow.close {
  .lines {
    &:before,
    &:after {
      top: 0;
      width: $button-size/1.8;
    }
    &:before {
      -webkit-transform: rotate3d(0,0,1,40deg);
      transform: rotate3d(0,0,1,40deg);
    }
    &:after {
      -webkit-transform: rotate3d(0,0,1,-40deg);
      transform: rotate3d(0,0,1,-40deg);
    }
  }
}

//arrow up only: just rotate by 90degrees
.lines-button.arrow-up.close {
  -webkit-transform: scale3d(.8,.8,.8) rotate3d(0,0,1,90deg);
  transform: scale3d(.8,.8,.8) rotate3d(0, 0, 1,90deg);
}
//three-lines to minus only
.lines-button.minus.close{
  .lines {
    &:before, &:after{
      -webkit-transform: none;
      transform: none;
      top:0;
      width: $button-size;
    }
  }
}

//three-lines to x
.lines-button.x.close{
  .lines {
    background: transparent;
    &:before, &:after{
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
      top:0;
      width: $button-size;
    }
    &:before{
      -webkit-transform: rotate3d(0,0,1,45deg);
      transform: rotate3d(0,0,1,45deg);
    }
    &:after{
      -webkit-transform: rotate3d(0,0,1,-45deg);
      transform: rotate3d(0,0,1,-45deg);
    }
  }
}

//three-lines to x method 2
.lines-button.x2{
  .lines{
    transition: background .3s .5s ease;

    &:before, &:after{
      //set transform origin
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
      transition: top .3s .6s ease, -webkit-transform .3s ease;
      transition: top .3s .6s ease, transform .3s ease;
    }
  }
}

.lines-button.x2.close{
  .lines {
    transition: background .3s 0s ease;
    background: transparent;

    &:before, &:after{
      transition: top .3s ease, -webkit-transform .3s .5s ease;//delay the formation of the x till the minus is formed
      transition: top .3s ease, transform .3s .5s ease;//delay the formation of the x till the minus is formed
      top:0;
      width: $button-size;
    }
    &:before{
      -webkit-transform: rotate3d(0,0,1,45deg);
      transform: rotate3d(0,0,1,45deg);
    }
    &:after{
      -webkit-transform: rotate3d(0,0,1,-45deg);
      transform: rotate3d(0,0,1,-45deg);
    }
  }
}

