// Section
// =======

.section {

  & + & {
    margin-top: $layout-gutter;
  }

  &.__head {
    margin-top: $layout-gutter;
  }

  &.__foot {
    margin-bottom: $layout-gutter;
  }

  &.__vertical{
      margin: 0;
      height: 100vh;
      > div{
        position: relative;
        top:50%;
        transform: translateY(-50%);
      }
  }

  &.__forground{
    z-index: 2;
    > div{
      position: relative;
    }
  }
  &.__background{
    z-index: 0;
      &.__large-fixed{
        position:fixed;
        width:100%;
      }
  }

  &.__black{
    background-color: #000;
    color:#fff;
  }

  &.__white_text{
    color:#fff;

  }

  &.__white{
    background-color: #fff;
    color: $color-grey;
  }

  &.__blue{
    background-color: $color-blue;
  }


  + &.__full {
    padding:$layout-gutter 0;

  }

}
