// Header
// ======

@mixin backgroundset($color,$opacity) {


  @if $opacity == 0 {
    $first : 1;
    $second : 0;
  }
  @else{
    $first : 0;
    $second : 1;
  }

  &:before{
    content:"";
    display:block;
    position:absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: $color;
    opacity: $first;
    z-index: -1;
    transition: opacity 0.5s;

  }

  &:after{
    content:"";
    display:block;
    position:absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background:  invert($color);
    opacity: 0.78;
    z-index: -1;
    transition: opacity 0.5s;

  }
}

.header,
.header-push {
  height: 80px;
}

.header {
  @include position(fixed, 0 0 none);
  padding: 24px 0;
  z-index: 1000;

  &.__switch{

    @include backgroundset(rgb(255,255,255),1);

    &.__on{
      &:before{
        opacity: 1;
        transition: opacity 0.5s;
      }

      &:after{
        opacity: 0;
        transition: opacity 0.5s;
      }
    }

    &.__off{
      &:before{
        opacity: 0;
        transition: opacity 0.5s;
      }

      &:after{
        opacity: 1;
        transition: opacity 0.5s;
      }
    }
  }

}
