.preloader{
	position: fixed;
	width:100%;
	height: 100vh;
	background: rgba(white,0.9);
	z-index: 10000;

	@include fadeIn(0.5s);


	&.animate-hide {
		@include fadeIn(0.5s);
	}

	&.animate-hide.ng-hide {
		@include fadeOut(0.5s);
	}

}




.loader {
	top:50%;
	margin: -40px auto;
	font-size: 10px;
	position: relative;
	text-indent: -9999em;
	border-top: 1.1em solid rgba($color-blue, 0.2);
	border-right: 1.1em solid rgba($color-blue, 0.2);
	border-bottom: 1.1em solid rgba($color-blue, 0.2);
	border-left: 1.1em solid $color-blue;
	-webkit-transform: translateZ(0);
	-ms-transform: translateZ(0);
	transform: translateZ(0);
	-webkit-animation: load8 1.1s infinite linear;
	animation: load8 1.1s infinite linear;
}
.loader,
.loader:after {
	border-radius: 50%;
	width: 7em;
	height: 7em;
}
@-webkit-keyframes load8 {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@keyframes load8 {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
