.video-holder{
	position: relative;
	display:block;
	width:100%;
	height: 100vh;
	background: black;

	.video{
		position: absolute;
		width:100%;
		top:0;
		bottom:0;
	}

	header{
		position:absolute;
		width:100%;
		text-align: left;
		z-index: 10;
		padding:20px;
		color:#fff;
		button{
			position: absolute;
			top:20px;
			right: 20px;
		}

	}
}

button.video{
	$icon-size:50px;
	letter-spacing: 1px;
	position:relative;

	span{
		margin-left: 14px;
	}

	i{
		position:absolute;
		left:8px;
		top:4px;
		font-size: 30px;
		@media #{$medium-up} {
      left: 9px;
      top: 6px;
			font-size: $icon-size;
		}
	}
}

.block-feature-animated {
  button.video{
    $icon-size:50px;
    i{
      position:absolute;
      left:8px;
      top:4px;
      font-size: 30px;
      @media #{$large-up} {
        left: 9px;
        top: 6px;
        font-size: $icon-size;
      }
    }
  }
}



videogular, [videogular] {
	width: 100%;
	height: 100%;
	position: relative;
	background-color: #000000;
	display: block;
	flex-direction: column;

	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: moz-none;
	-ms-user-select: none;
	user-select: none;

	button {
		cursor: pointer;
	}

	&.fullscreen {
		position: fixed;
		left: 0;
		top: 0;
	}

	vg-media {
		width: 100%;
		height: 100%;
		max-width: 100%;
		max-height: 100%;

		display: block;
	}

	video {
		width: 100%;
		height: 100%;
		max-width: 100%;
		max-height: 100%;
	}

	.iconButton {
		color: #FFFFFF;
		@extend .fa;

		background: none;
		padding: 6px;
		border: none;
    margin: 0;
	}

	.iconButton:focus {
		border: 1px solid black;
	}

	/*****************/
	/* Poster plugin */
	/*****************/
	vg-poster {
		width: 100%;
		height: 100%;
		max-width: 100%;
		max-height: 100%;
		position: absolute;
		display: block;
		z-index: 1;
		top: 0;
	}

	vg-poster img {
		width: auto;
		height: auto;
		max-width: 100%;
		max-height: 100%;

		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		margin: auto;
		position: absolute;
	}

	vg-poster img.pause,
	vg-poster img.play {
		display: none;
	}

	vg-poster img.stop {
		display: block;
	}

	/**********************/
	/* OverlayPlay plugin */
	/**********************/
	vg-overlay-play {
		width: 100%;
		height: 100%;
		max-width: 100%;
		max-height: 100%;
		position: absolute;
		z-index: 2;
		top: 0;
	}

	vg-overlay-play .play:before {
		content: "\e000";
	}

	vg-overlay-play .overlayPlayContainer {
		font-size: 100px;
		width: 100%;
		height: 100%;
		max-width: 100%;
		max-height: 100%;
		position: absolute;
		display: table;
		cursor: pointer;

		zoom: 1;
		filter: alpha(opacity=100);
		opacity: 1;
	}

	vg-overlay-play .overlayPlayContainer div {
		vertical-align: middle;
		text-align: center;
		display: table-cell;
	}

	/*********************/
	/* Controlbar plugin */
	/*********************/
	vg-controls {
		width: 100%;
		height: 50px;
		display: block;
		position: absolute;
		z-index: 3;
		bottom:80px;
	}

	vg-controls .controls-container {
		width: 100%;
		height: 50px;
		background-color: black;
		position: absolute;
		display: table;

		zoom: 1;
	}

	vg-play-pause-button {
		display: table-cell;
		width: 50px;
		vertical-align: middle;
		text-align: center;
		cursor: pointer;
	}

	vg-time-display {
		color: #FFFFFF;
		display: table-cell;
		font-family: $consolas;
		font-size: 18px;
		width: 75px;
		vertical-align: middle;
		text-align: center;
		cursor: default;
	}

	.vgTimeDisplay {
		display: table-cell;
		font-family: Arial;
		font-size: 18px;
		width: auto;
	}

	vg-scrub-bar {
		width: auto;
		display: table-cell;
		cursor: pointer;
		vertical-align: middle;
	}

	vg-scrub-bar [role=slider] {
		margin-top: 24px;
		margin-bottom: 24px;
		height: 2px;
		background-color: black;
	}

	vg-scrub-bar :focus vg-scrub-bar-current-time {
		border-right: 6px black groove;
	}

	vg-scrub-bar-current-time {
		background-color: #FFFFFF;
		width: 100%;
		height: 100%;
		display: block;
		cursor: pointer;
	}

	vg-scrub-bar-cue-points {
		width: 100%;
		height: 100%;
		display: block;
		position: relative;

		.cue-point-timeline {
			color: #FFFFFF;
			padding-top: 3px;

			.cue-point {
				position: absolute;
				height: 3px;
				background-color: #FFFFFF;
			}
		}
	}

	vg-playback-button {
		display: table-cell;
		width: 50px;
		vertical-align: middle;
		text-align: center;
		cursor: pointer;
	}

	vg-volume {
		display: table-cell;
		width: 50px;
		vertical-align: middle;
		text-align: center;
		cursor: pointer;
	}

	vg-volume-bar {
		width: 50px;
		height: 100px;
		top: -100px;
		margin-left: -25px;
		vertical-align: middle;
		text-align: center;
		position: absolute;
		cursor: pointer;
	}

	/* IE10 hack */
	@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
		vg-volume-bar {
			zoom: 1;
			filter: alpha(opacity=50);
			opacity: 0.5;
		}
	}

	vg-fullscreen-button {
		display: table-cell;
		width: 50px;
		vertical-align: middle;
		text-align: center;
		cursor: pointer;
	}

	vg-volume-bar .verticalVolumeBar {
		width: 50px;
		height: 100px;
		background-color: #000000;
		position: absolute;
	}

	vg-volume-bar .volumeBackground {
		width: 20px;
		height: 70px;
		left: 15px;
		top: 15px;
		background-color: #222222;
		position: absolute;
	}

	vg-volume-bar .volumeValue {
		width: 20px;
		height: 100%;
		background-color: #FFFFFF;
		position: absolute;
	}

	vg-volume-bar .volumeClickArea {
		width: 20px;
		height: 100%;
		position: absolute;
	}

	vg-controls .hide-animation {
		animation: hideControlsAnimationFrames ease-out 0.5s;
		animation-iteration-count: 1;
		animation-fill-mode: forwards; /*when the spec is finished*/
		-webkit-animation: hideControlsAnimationFrames ease-out 0.5s;
		-webkit-animation-iteration-count: 1;
		-webkit-animation-fill-mode: forwards; /*Chrome 16+, Safari 4+*/
		-moz-animation: hideControlsAnimationFrames ease-out 0.5s;
		-moz-animation-iteration-count: 1;
		-moz-animation-fill-mode: forwards; /*FF 5+*/
		-o-animation: hideControlsAnimationFrames ease-out 0.5s;
		-o-animation-iteration-count: 1;
		-o-animation-fill-mode: forwards; /*Not implemented yet*/
		-ms-animation: hideControlsAnimationFrames ease-out 0.5s;
		-ms-animation-iteration-count: 1;
		-ms-animation-fill-mode: forwards; /*IE 10+*/
	}

	@keyframes hideControlsAnimationFrames {
		0% {
			opacity: 0.5;
		}
		100% {
			opacity: 0;
		}
	}

	@-moz-keyframes hideControlsAnimationFrames {
		0% {
			opacity: 0.5;
		}
		100% {
			opacity: 0;
		}
	}

	@-webkit-keyframes hideControlsAnimationFrames {
		0% {
			opacity: 0.5;
		}
		100% {
			opacity: 0;
		}
	}

	@-o-keyframes hideControlsAnimationFrames {
		0% {
			opacity: 0.5;
		}
		100% {
			opacity: 0;
		}
	}

	@-ms-keyframes hideControlsAnimationFrames {
		0% {
			opacity: 0.5;
		}
		100% {
			opacity: 0;
		}
	}

	vg-controls .show-animation {
		animation: showControlsAnimationFrames ease-out 0.5s;
		animation-iteration-count: 1;
		animation-fill-mode: forwards; /*when the spec is finished*/
		-webkit-animation: showControlsAnimationFrames ease-out 0.5s;
		-webkit-animation-iteration-count: 1;
		-webkit-animation-fill-mode: forwards; /*Chrome 16+, Safari 4+*/
		-moz-animation: showControlsAnimationFrames ease-out 0.5s;
		-moz-animation-iteration-count: 1;
		-moz-animation-fill-mode: forwards; /*FF 5+*/
		-o-animation: showControlsAnimationFrames ease-out 0.5s;
		-o-animation-iteration-count: 1;
		-o-animation-fill-mode: forwards; /*Not implemented yet*/
		-ms-animation: showControlsAnimationFrames ease-out 0.5s;
		-ms-animation-iteration-count: 1;
		-ms-animation-fill-mode: forwards; /*IE 10+*/
	}

	@keyframes showControlsAnimationFrames {
		0% {
			background-color: rgba(0, 0, 0, 0);
		}
		100% {
			background-color: rgba(0, 0, 0, 0.5);
		}
	}

	@-moz-keyframes showControlsAnimationFrames {
		0% {
			background-color: rgba(0, 0, 0, 0);
		}
		100% {
			background-color: rgba(0, 0, 0, 0.5);
		}
	}

	@-webkit-keyframes showControlsAnimationFrames {
		0% {
			background-color: rgba(0, 0, 0, 0);
		}
		100% {
			background-color: rgba(0, 0, 0, 0.5);
		}
	}

	@-o-keyframes showControlsAnimationFrames {
		0% {
			background-color: rgba(0, 0, 0, 0);
		}
		100% {
			background-color: rgba(0, 0, 0, 0.5);
		}
	}

	@-ms-keyframes showControlsAnimationFrames {
		0% {
			background-color: rgba(0, 0, 0, 0);
		}
		100% {
			background-color: rgba(0, 0, 0, 0.5);
		}
	}

	/* Controlbar icons */
	vg-play-pause-button .play:before {
		@extend .fa-play;
	}

	vg-play-pause-button .pause:before {
		@extend .fa-pause;
	}

	vg-mute-button {
		width: 50px;
		display: block;
	}

	vg-mute-button .level3:before {
		@extend .fa-volume-up;
	}

	vg-mute-button .level2:before {
    @extend .fa-volume-down;
	}

	vg-mute-button .level1:before {
    @extend .fa-volume-down;
	}

	vg-mute-button .level0:before {
    @extend .fa-volume-down;
	}

	vg-mute-button .mute:before {
    @extend .fa-volume-off;
	}

	vg-fullscreen-button .enter:before {
		@extend .fa-desktop;
	}

	vg-fullscreen-button .exit:before {
		@extend .fa-compress;
	}

	/********************/
	/* Buffering plugin */
	/********************/
	vg-buffering {
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 4;
		top: 0;
	}

	vg-buffering .bufferingContainer {
		width: 100%;
		position: absolute;
		cursor: pointer;
		top: 50%;
		margin-top: -50px;

		zoom: 1;
		filter: alpha(opacity=60);
		opacity: 0.6;
	}

	/* Loading Spinner
	 * http://www.alessioatzeni.com/blog/css3-loading-animation-loop/
	 */
	vg-buffering .loadingSpinner {
		background-color: rgba(0, 0, 0, 0);
		border: 5px solid rgba(255, 255, 255, 1);
		opacity: .9;
		border-top: 5px solid rgba(0, 0, 0, 0);
		border-left: 5px solid rgba(0, 0, 0, 0);
		border-radius: 50px;
		box-shadow: 0 0 35px #FFFFFF;
		width: 50px;
		height: 50px;
		margin: 0 auto;
		-moz-animation: spin .5s infinite linear;
		-webkit-animation: spin .5s infinite linear;
	}

	vg-buffering .loadingSpinner .stop {
		-webkit-animation-play-state: paused;
		-moz-animation-play-state: paused;
	}

	@-moz-keyframes spin {
		0% {
			-moz-transform: rotate(0deg);
		}
		100% {
			-moz-transform: rotate(360deg);
		}
	}

	@-moz-keyframes spinoff {
		0% {
			-moz-transform: rotate(0deg);
		}
		100% {
			-moz-transform: rotate(-360deg);
		}
	}

	@-webkit-keyframes spin {
		0% {
			-webkit-transform: rotate(0deg);
		}
		100% {
			-webkit-transform: rotate(360deg);
		}
	}

	@-webkit-keyframes spinoff {
		0% {
			-webkit-transform: rotate(0deg);
		}
		100% {
			-webkit-transform: rotate(-360deg);
		}
	}

	/**********************/
	/* IMA ads plugin */
	/**********************/
	vg-ima-ads {
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 5;
		top: 0;
		display: none;
	}
}
