[class*="block-grid-"] {
	&.text-center {
		//font-size: 0;
		> li {
			float: none;
			display: inline-block;
			margin-bottom: 50px;
      vertical-align: top;
		}
	}
}

#team{
  h1{
    margin-top: 50px;
  }
}

.rich .team-block{
  @media #{$small-only} {
     .wrap:first-child {
      padding-top: 50px;
    }
  }
  h1, h1:first-child{
    margin-top: 50px;
  }
  p{
    font-size: 18px;
    @media #{$medium-up} {
      font-size: 20px;
    }
    @media #{$large-up} {
      font-size: 24px;
    }
  }
}

.consultants{
  h4{
    font-size:24px;
    margin:10px 0 0 0;
  }
  sub{
    font-size:20px;
    bottom:0;
    color:#a1a1a1;
  }
  p{
    margin:12px 0;
    span{
      display: block;
      font-weight: 400;
    }
  }
  ul{
    padding-left: 0;
  }
  a{
    text-decoration: none;
    color: $color-blue;
    h4{
      color: $color-blue;
    }
  }
  .btn{
    text-transform: none;
    color:#000;
    padding: 13px 60px;
    margin-top: 10px;
  }
}

.revealbutton{
	position: relative;
	width:100%;
	padding:50px;

	&:before{
		content: '';
		position:absolute;
		top:50%;
		left: 0;
		width: 100%;
		height: 50%;
		background: $color-light-grey;
	}

	.button-plus{
		width:80px;
		height:80px;
		margin:0 auto;
		cursor: pointer;
		i{
			position:relative;
			color:white;
			font-size: 50px;
			width: 100%;
			height: 100%;
			&:after{
				position:absolute;
				content: '\f067';
				line-height: 72px;
				background: $color-blue;
				width:80px;
				height:80px;
				top:0;
				left:0;
				border-radius: 50%;
				border: 5px solid white;

			}
		}

	}

  .button-minus{
    width:80px;
    height:80px;
    margin:0 auto;
    cursor: pointer;
    i{
      position:relative;
      color:white;
      font-size: 50px;
      width: 100%;
      height: 100%;
      &:after{
        position:absolute;
        content: '\f068';
        line-height: 72px;
        background: $color-blue;
        width:80px;
        height:80px;
        top:0;
        left:0;
        border-radius: 50%;
        border: 5px solid white;

      }
    }

  }

}
.staff{
  h4{
    margin-bottom:0;
  }
	background: $color-light-grey;
	padding-top:50px;
  ul{
    padding-left: 0;
  }
}
