/* pin */
.pin {
	position: relative;
	height: 100vh;
	width: 100%;
	top: 0;
}

.pin-pin {
	position: fixed !important;
	bottom: 0;
	transform: translateZ(0);
}

.pin-unpin {
	position: absolute !important;
	bottom: 0;
	top: auto;
}

.pin-txt {
	position: absolute;
	top: 0;
	margin: 0;
	text-align: center;
	width: 100%;
}

.pin-cont {
	position: relative;
	height: 1400px;
}
