.block-gettinghere{

  position:relative;
  background: white;

  .tab-nav{
    ul{

      li{
        margin: 0;
        a.button{
          margin-bottom: 0;
        }
      }

    }
  }

  .bt-select-map {
    padding:1rem;
    font-size: 1rem;
    @media #{$medium-up}{
      padding: 1.125rem 2.25rem 1.1875rem 2.25rem;
      font-size: 1.25rem;
    }
  }

  .pointer {
    position: absolute;
    width: 20px;
    height: 20px;
    bottom: -10px;
    z-index: 2;
    opacity: 1;
    &:before {
      content: ' ';
      display: block;
      width: 20px;
      height: 20px;
      transform: rotate(45deg);
      background: white;
      margin: 0 auto;
    }
  }


}



.google-map{
  position:relative;

  .angular-google-map-container,.map-holder {
    background: $color-grey;
    width:100%;
    height: 44vh;
    @media #{$medium-up} {
      height: 50vh;
    }
    @media #{$large-up} {
      height: 44vh;
    }
  }
}

.dirBtn-holder{
  position:absolute;
  text-align: center;
  width:100%;
  z-index: 2;
  bottom:0;

  .dirBtn{
    background: $color-blue;
    padding: 10px 0;
    cursor: pointer;
    letter-spacing: 3px;

    &:not(:hover) {
      transition: 1s;
      a {
        color:white;
        transition: 1s;
      }
    }

    &:hover{
      background: white;
      @include fadeIn();
      a{
        color:$color-blue;
        @include fadeIn();
      }
    }
    .__btn span {
      padding-top:0;
    }
  }

}

