#feature{
	.bg{
		opacity: 0.3;
	}

	.large-fixed{
		position: fixed;
	}
}

#home{
	.parallax{
		position:relative;
	}
	.large-fixed{
		position: fixed;
	}
}

.block-feature{

  height:100vh;

  .section.__vertical{

    @media #{$landscape} {
      min-height: 768px;
    }



    h1{
      font-size: 30px;
      @media #{$medium-up} {
        font-size: 72px;
      }
      @media #{$large-up} {
        font-size: 92px;
      }
    }

    p{
      font-size: 18px;
      @media #{$medium-up} {
        font-size: 20px;
      }
      @media #{$large-up} {
        font-size: 24px;
      }
    }
  }
}

.block-parallax-video{
  .bg-holder {
    height:100%;

  }
}

.block-feature-animated{
  .section.__vertical{
    min-height: 640px;
    @media #{$medium-up} {
      min-height: 768px;
    }
    h1{
      font-size: 30px;
      @media #{$medium-up} {
        font-size: 40px;
      }
      @media #{$large-up} {
        font-size: 72px;
      }
    }
    p{
      font-size: 18px;
      @media #{$large-up} {
        font-size: 24px;
      }

    }
  }
  .bg-holder {
    height:100%;
  }
}

.device-android {
  #therapies{
    @media #{$small-only} {
      .section.__vertical{
        height:110vh;
      }
    }
  }

}

