$inSize:70px;

.indicator-scroll{
	position: absolute;
	width: 100%;
	height: $inSize;
	left:0;
	bottom:60px;
	opacity: 0;
  @media #{$medium-up}{
    bottom:60px;
    @media only screen
    and (min-device-width: 768px)
    and (max-device-width: 1024px)
    and (orientation: portrait)
    and (-webkit-min-device-pixel-ratio: 1) {
      bottom:80px;
    }
    @media only screen
    and (min-device-width: 768px)
    and (max-device-width: 1024px)
    and (orientation: landscape)
    and (-webkit-min-device-pixel-ratio: 1) {
      bottom:30px;
    }

  }
	.holder{
		border: 2px solid black;
		border-radius: $inSize / 2;
		background: white;
		width: $inSize;
		height: $inSize;
		margin: 0 auto;
    position: relative;
	}

	i{
		color:black;
		font-size: $inSize * 0.3;
		//padding: $inSize * 0.15
	}
  span{
    display: block;
    font-size: 11px;
    text-transform: uppercase;
    font-weight: bold;
    margin: 0 auto;
    color:#000;
    line-height: 12px;
    padding-top: 22px;
    margin-bottom: -2px;
  }
}

.indicator-top{
  display: none;
  @media #{$large-up} {
    display: block;
    position: fixed;
    width: 100%;
    height: 50px;
    left:0;
    bottom:10px;
    opacity: 0;
    z-index: 100;
    .holder{
      border: 2px solid $white;
      border-radius: 25px;
      background: #007bc4;
      width: 50px;
      height: 50px;
      position: absolute;
      right:10px;
    }

    i{
      color:white;
      font-size: 20px;
      padding: 12px 16px;
      margin:0 auto;
    }
  }

}
