//Info Sections

.info{
	.tb{
    height: 25vh;
		@media #{$small-only} {
			margin-bottom: 40px;
		}
	}
	font-size: 18px;
	i{
		font-size: 38px;
		margin: 5px;

		img{
			display: inline;
		}
	}

	ul{
		list-style: none;
		margin: 0;
		padding:0;
		li{
			margin-bottom: 30px;
		}
	}
  .medium-3{
    font-size:18px;
    @media #{$medium-up} {
      font-size:16px;
    }
    @media #{$large-up} {
      font-size:18px;
    }

  }

  .info-link{
    color: $color-blue;
    text-decoration: none;
  }

	.transport{
		.row{
			padding:0;
		}
		.columns{
			cursor: pointer;
			padding:0 0;
		}

		a{
			display: block;
			position: relative;
			color:white;
			border: 0px solid ;
			transition: color 1s cubic-bezier(0.215, 0.610, 0.355, 1.000);
      i{
        font-size: 30px;
        @media #{$medium-up} {
          font-size: 40px;
        }
        @media #{$large-up} {
          font-size: 75px;
        }
      }
      @media #{$medium-up} {
        min-height: 25vh;
      }
				&:after{
					content:'';
					display: block;
					position: absolute;
					background: white;
					width: 100%;
					height: 0;
					top:0;
					left: 0;
					z-index:0;
					transition: height 1s cubic-bezier(0.215, 0.610, 0.355, 1.000);
				}

			&:hover{
				color: $color-blue;
				transition: color 1s cubic-bezier(0.215, 0.610, 0.355, 1.000);
					&:after {
						height: 100%;
						transition: height 0.5s cubic-bezier(0.215, 0.610, 0.355, 1.000);

					}

			}
		}
		.tb{
			position: absolute;
			height: 25vh;
			z-index:1 ;

		}
    .tb_c{
      font-family: "Open-Sans";
      font-size: 11px;
      font-weight: 800;
      text-transform: uppercase;
      letter-spacing: 0.2em;
      padding:0 20px;
    }
    @media #{$medium-up}{
      .tb_c{
        font-size: 12px;
        padding:0 15px;
      }
    }
    @media #{$large-up}{
      .tb_c{
        font-size: 14px;
        padding:0 10px;
      }
    }
	}

}

.direction {
  h2 {
    font-size: 24px;
    padding:40px 0 20px 0;
    @media #{$large-up}{
      font-size: 42px;
    }
    &:after {
      border: none;
    }
  }
}
