.journey{
	margin-top:50px;

  .line{
    position:absolute;
    width:100%;
    height: 110%;
    left:0;
    z-index: 0;
    > div{

      width: 1px;
      height: 100%;
      margin: 0 auto;
      &:before{
        content:'';
        display:block;
        width:10px;
        height: 100%;
        background-color: grey;
        margin-left: -5px;
      }
    }
  }

	ul{
    position: relative;
		list-style: none;
		margin: 50px 0 20px 0;
		padding:0;

		.odd, .even{
			text-align: left;
		}

    .content{
      margin-bottom: 30px;
    }

    @media #{$small-only} {
      .odd,.even{
        text-align: center;
      }
    }

		@media #{$medium-up} {
			.odd{
				text-align: right;
			}
			.even{
				text-align: left;
			}
		}

		li{

      $is: 40px;
      $ip: 10px;
      position:relative;

      i{
        color:$color-blue;
        padding: 0 10px;
        font-size: $is;
        @media #{$small-only}{
          padding:0;
          &:before{
            display:none;
          }
        }
      }

      .m-counter{
        margin: 10px auto;
        background: $color-blue;
        font-family: $headings;
        font-weight: $bold;
        color:white;
        width: 60px;
        height: 60px;
        padding: 16px;
        text-align: center;
        border-radius: 30px;
      }


			.counter{
				position:absolute;
        z-index: 1;
				right:-45px;
				color:white;
				font-family: $headings;
				font-weight: $bold;
				font-size: 20px;
				text-align: center;
				background: $color-blue;
				width: 60px;
				height: 60px;
				padding: 16px;
				border-radius: 40px;
        @media #{$large-up} {
          width: 80px;
          height: 80px;
          font-size: 30px;
          right:-55px
        }
			}

			&.odd{

        i{
          @media #{$medium-up} {
            margin-right: - (($is + $ip) + 8);
          }
        }
				.counter{
          @media #{$medium-up} {
            float: right;
          }
				}
			}


			&.even{
        i{
          @media #{$medium-up} {
            margin-left: - (($is + $ip) + 8);
          }
        }

				.counter{
					right:-30px;
          @media #{$large-up} {
            right:-40px;
          }
				}
			}

      h4{
        font-size: 24px;
        font-weight: 300;
        @media #{$medium-up} {
          margin-top:15px!important;
        }
        @media #{$large-up} {
          font-size: 36px;
          margin-top:25px!important;
        }
      }
		}

	}
  h3{
    font-size: 30px;
    @media #{$medium-up} {
      font-size: 60px;
    }
  }
}
