// Logotype
// ========

.logo-holder{
  text-align: right;
}

.logo {
  position: relative;
  @include image-size('project/logo/logo.png');
  background-image: inline('project/logo/logo.png');
  background-repeat: no-repeat;
  background-size: 100% auto;
  display: inline-block;


  @media #{$small-only} {
      width:260px;
      @media only screen and (max-width: 320px) {
        width:240px;
      }
  }


  &:after {
    content: '';
    display: block;
  }

  // Modifiers
  // ---------

  &.__s {
    width: 102px;
  }

  &.__m {
    width: 154px;
  }

}


.header{
  &.__on{

    .logo {
      &:after {
        content: "";
        position: absolute;
        right: 0;
        @include image-size('project/logo/logo_b.png');
        background-image: inline('project/logo/logo_b.png');
        background-repeat: no-repeat;
        background-size: 100% auto;
        display: inline-block;
        @include fadeIn();

        @media #{$small-only} {
          width:260px;
          @media only screen and (max-width: 320px) {
            width:240px;
          }
        }

      }
    }
   &.menu-open{

     .logo-holder{
       z-index: 199;
       @include fadeIn();
     }

     &.menu-docked{
       .logo-holder {
         margin-top: 80px;
       }
     }

   }
  }
}
