.bg-holder{
  position:absolute;
  background-color: black;
  width:100%;
  height: 100vh;
  overflow:hidden;
}

@mixin background-cover($backgrounds...){

	@each $elem in $backgrounds{
   &.#{$elem}{
		 position: absolute;
		 width:100%;
		 height: 100%;
		 background-color: black ;
		 background: url('/assets/images/project/main_bg/' + #{$elem} +'.jpg') no-repeat center center;
		 background-size: cover;
		}
	}
}

.bg-cover{
	position: absolute;
	width:100%;
	height: 100%;
	background-position: center center;
	background-color: black;
	background-size: cover;
  @media #{$small-only} {
    opacity: 0.4!important;
  }
}

.bg{
	@include background-cover('GettingHere-Main');
	@include background-cover('bolsover');
	@include background-cover('stanmore');
	@include background-cover('assisted_transport');

	@include background-cover('grad_grey');
	@include background-cover('service_journey');
	@include background-cover('service_foot');

	@include background-cover('Outpatients-Main');
	@include background-cover('Outpatients-Stanmore');
	@include background-cover('Outpatients-Bolsover');
	@include background-cover('Outpatients-PreoperativeAssessment');

	@include background-cover('Inpatients');
	@include background-cover('Inpatients-admission');
